@import "../common/styles/sass/variable";

.nav {
  width: 380px;
  display: flex;
  justify-content: space-between;
  z-index: 100;

  .link {
    font-size: 1em;
    font-weight: 500;
    color: $textColor;
    text-decoration: none;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 4px;
      top: 36px;
      background: 0 0;
      transition: all .5s ease-in-out;
    }

    &:hover {
      transition: all .3s ease-in-out;
      color: $primaryColor;

      &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 4px;
        top: 36px;
        background: $primaryColor;
        transition: all .3s ease-in-out;
      }
    }

    &:active {
      transition: all .3s ease-in-out;
      color: $primaryColor;
    }

    &:focus {
      transition: all .3s ease-in-out;
      color: $primaryColor;
    }
  }

}
